async function verifyTonProof(address, public_key, signature, domain) {
  try {
    // 解析地址
    const parsedAddress = Address.parse(address);

    // 构建需要验证的消息
    const message = `${domain}\n${parsedAddress.toString(true, true, true)}`;

    // 从 proof 中提取公钥
    const publicKey = Buffer.from(public_key,'hex');

    // 将签名转换为 Buffer
    // const signatureBuffer = utils.hexToByteArray(signature);
    const signatureBuffer = Buffer.from(signature,'base64');

    // 验证签名
    return signVerify(Buffer.from(message), signatureBuffer, publicKey);
  } catch (error) {
    console.error("Error verifying TON proof:", error);
    return false;
  }
}

// 使用示例
const address = "EQArzP5prfRJtDM5WrMNWyr9yUTAi0c9o6PfR4hkWy9UQXHx";
